:root{
  --rotacao-225: 225deg;
  --rotacao-45negativo: -45deg;

}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 50px;
}

.container {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-areas: "a1 a1" "a2 a2" "a3 a3";
  grid-template-rows: 13vh 74vh 13vh;
}

#header {
  background-color: rgb(36, 36, 184);
  grid-area: a1;
  text-align: center;
}

#header h1 {
  color: white;
  filter: drop-shadow(5px 5px 5px black);
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
}

.principal {
  grid-area: a2;
  display: grid;
  grid-template-areas: "lista form from" "lista form from";
  grid-template-columns: 40vw;
}

.lista {
  box-shadow: 1px 0px 10px black;
  overflow-y: auto;
  grid-area: lista;
  /* text-align: center; */
}


#formulario {
  grid-area: form;
}

#containerList {
  margin: 1rem 0 0 1rem;
}
.lista ul{
  box-shadow: 5px 5px 10px  black;
}
.lista ul>li {
  font-size: .8rem;
  color: rgb(41, 40, 40);
  font-weight: bold;
  list-style: none;
  cursor: pointer;
  padding: .1rem;
  display: flex;
  justify-content: space-between;
}
.lista ul>li:not(:nth-child(2n)){
  background-color: rgba(36, 125, 184, 0.374);
}
.lista ul>li:nth-child(2n){
  background-color: rgba(136, 145, 151, 0.374);
}

.done {
  text-decoration: line-through;
}

#form {
  width: 18rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: auto;
}

#form>input {
  text-align: center;
  width: 9rem;
  border-radius: .2rem;
  border-top: none;
  filter: drop-shadow(5px 5px 5px black);
  font-size: .8rem;
  padding: .2rem;
  color: rgb(59, 58, 58);
}
.removerItem{
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
.lixo{
  width: 1.2rem;
  filter: drop-shadow(1px 1px 3px white);
}
.btn {
  display: flex;
  flex-wrap: wrap;
}

.btn>input {
  margin: 0 .5rem;
  cursor: pointer;
  font-size: .8rem;
  padding: .4rem;
  border: none;
  border-radius: .2rem;
  color: white;
  background-color: rgba(72, 68, 68, 0.672);
}

#footer {
  background-color: rgb(36, 36, 184);
  grid-area: a3;
  text-align: center;
}

#footer p {
  color: white;
  font-size: 1.1rem;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.lista{
  overflow-x: hidden;
}
@media screen and (max-width:1200px) {
  * {
    font-size: 35px;
  }
  .removerItem{
    display: none;
  }
  .icon-navegacao::before {
    content: '';
    display: block;
    width: .8rem;
    height: .2rem;
    background-color: black;
    rotate: var(--rotacao-225);
    position: relative;
    transition-duration: .5s;
  }
  .lista{
    overflow-x: hidden;
  }
  .lista ul{
    width: 16rem;
    margin-top: 4rem;
  }
  .lista ul>li {
    font-size: .8rem;
    color: rgb(41, 40, 40);
    font-weight: bold;
    list-style: none;
    cursor: pointer;
    padding: .1rem;
    display: flex;
    justify-content: space-between;
  }
  .lista ul>li {
    color: rgb(41, 40, 40);
    font-weight: bold;
    list-style: none;
    cursor: pointer;
    padding: .3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .icon-navegacao::after {
    content: '';
    display: block;
    width: .8rem;
    height: .2rem;
    background-color: black;
    rotate: var(--rotacao-45negativo);
    position: relative;
    top: .2rem;
    transition-duration: .5s;
  }
  .rodar::after{
    rotate: 45deg;
  }
  .rodar::before{
    rotate: -225deg;
  }
  #lista{
   position: relative;
  }
  #seta{
    background-color: white;
    padding: .8rem;
    float: right;
    border: none;
  }
  .principal{
    transition-duration: 1s ;
    overflow: hidden;
  }
  .btn-responsivo {
    overflow: hidden;
    grid-template-columns: 100vw;
    transition-duration: 1s;
    text-align: center;
  }
}

@media screen and (max-width:800px) {
  * {
    font-size: 20px;
  }

 

  #form {
    width: 10rem;
    margin: auto;
  }

  .btn {
    flex-direction: column;
    gap: 20px;
  }

  .lista ul>li {
    font-size: 1rem;
    line-height: 28px;
  }
}